@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&family=Spectral:ital,wght@1,700&display=swap');

* {
    box-sizing: border-box;
    text-decoration: none;
}

body {
    padding-top: 4rem;
    margin: 0px;
    color: #333;
    --type-first: Helvetica, Arial, sans-serif;
    --type-second: 'Spectral', Georgia;
    font-family: var(--type-first);
}

h1,
h2,
h3,
h4,
p {
    margin: 0px;
}

ul,
li {
    list-style: none;
    padding: 0px;
    margin: 0px;
}

img {
    display: block;
    max-width: 100%;
}

button,
input {
    display: block;
    margin-bottom: 1rem;
    font-family: var(--type-first);
    color: #333;
}

a {
    text-decoration: none;
    color: #333;
}

.container {
    max-width: 50rem;
    padding: 0 1rem;
    margin: 0 auto;
}

.mainContainer {
    margin-top: 4rem;
}

.title {
    font-family: var(--type-second);
    line-height: 1;
    font-size: 3rem;
    margin: 1rem 0;
    position: relative;
    z-index: 1;
}

.title::after{
    content: '';
    display: block;
    width: 1.5rem;
    height: 1.5rem;
    background: #fb1;
    position: absolute;
    bottom: 5px;
    left: -5px;
    border-radius: .2rem;
    z-index: -1;
}

.animeLeft {
    opacity: 0;
    transform: translateX(-20px);
    animation: animeLeft .3s forwards;
}

@keyframes animeLeft {
    to {
        opacity: 1;
        transform: initial;
    }
}

.App {
    display: flex;
    flex-direction: column;
    min-height: calc(100vh + 10rem);
}

.AppBody {
    flex: 1;
}

.VictoryContainer {
    height: initial !important;
}
